import React from 'react';
import { createForm, formShape } from 'rc-form';
import '../styles/subscribe.css';

class SubscribePage extends React.Component {
  static propTypes = {
    form: formShape
  };
  constructor(props) {
    super(props);
  }
  onSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((error, value) => {
      console.log(this.props.form.getFieldError('username'))
      console.log(error, value);
    });
  }
  getError = (key) => {
    const errs = this.props.form.getFieldError(key);
    let res = null;
    if (errs) {
      res = errs.join(',');
    }
    return res;
  }
  render() {
    const { getFieldDecorator, getFieldError } = this.props.form;
    const userNameOptions = {
      rules: [{
        required: true,
        message: '请输入姓名',
      },]
    };
    const mobileOptions =  {
      rules: [{
        required: true,
        type: 'number',
        message: '请输入正确手机号'
      }]
    };
    const stuIdOptions = {
      rules: [{
        required: true,
        type: 'number',
        message: '请输入正确的学号'
      }]
    };
    const addressOptions = {
      rules: [{
        required: true,
        type: 'string',
        message: '请输入宿舍地址'
      }]
    }
    const roteOptions = {
      rules: [{
        required: true,
        message: '请选择路线'
      }]
    }
    const startDateOptions = {
      rules: [{
        required: true,
        message: '请选择出发日期'
      }]
    }
    const startTimeOptions = {
      rules: [{
        required: true,
        message: '请选择出发时间'
      }]
    }
    return (
      <div className="subscribe">
        <form>
          <div>
            <label>
              <p>姓名</p>
              {getFieldDecorator('username', userNameOptions)(
                <input className="sub-input" />
              )}
            </label>
            <p>{this.getError('username')}</p>
          </div>

          <div>
            <label>
              <p>姓名</p>
              {getFieldDecorator('mobile', mobileOptions)(
                <input className="sub-input" type="number" />
              )}
            </label>
            <p>{this.getError('mobile')}</p>
          </div>
          
          <div>
            <label>
              <p>姓名</p>
              {getFieldDecorator('mobile', mobileOptions)(
                <input className="sub-input" type="number" />
              )}
            </label>
            <p>{this.getError('mobile')}</p>
          </div>
          <button onClick={this.onSubmit}>提交</button>
        </form>
      </div>
    )
  }
}

export default createForm()(SubscribePage);